import {
  Injectable
} from '@angular/core';
import {
  HttpClient,
  HttpRequest,
  HttpResponse,
  HttpHeaders,
  HttpParams,
  HttpErrorResponse
} from '@angular/common/http';
import {
  Observable
} from 'rxjs/Observable';
import {
  map
} from 'rxjs/operators/map';
import {
  filter
} from 'rxjs/operators/filter';
import {
  constructDependencies
} from '@angular/core/src/di/reflective_provider';
@Injectable()
export class AccountService {
  constructor(private http: HttpClient) {}
  login(username: string, password: string): Observable < any > {

    var __headers = new HttpHeaders({
      'Content-Type': 'application/x-www-urlencoded'
    });
    var __body = {
      username: username,
      password: password,
      grant_type: 'password'

    };
    const formBody = Object.keys(__body).map(key => encodeURIComponent(key) + '=' + encodeURIComponent(__body[key])).join('&');
    let req = new HttpRequest < any > (
      'POST',
      'http://localhost:8989/api/login',
      formBody, {
        headers: __headers

      });

    return this.http.request < any > (req).pipe(
      filter(_r => _r instanceof HttpResponse),
      map(_r => {
        let _resp = _r as HttpResponse < any > ;
        let _body = _resp.body;
        console.log(_body);
        if (_resp.ok) {
          localStorage.clear();
          console.log(_body);
          Object.keys(_body).map(key => localStorage.setItem(key, _body[key]));
        }
        return _resp.clone({
          body: _body
        }) as HttpResponse < any > ;
      })
    );
  }


}



